.pricing {
  width: 100%;
  padding: 6rem 1rem;
  background: black;
  /* text-decoration: aqua; */
}
/* @media screen and(min-width:750px) { */
.card-container {
  max-width: 1140px;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}

@media screen and (max-width: 750px) {
  .card-container {
    max-width: 90%;
    margin: auto;
    /* display: grid; */
    grid-template-columns: 1fr;
    /* grid-gap: 40px; */
  }
}
.card h3 {
  font-size: 1.4rem;
  padding: 1rem;
}
.card p {
  padding: 10px 0;
}
.bar {
  border-bottom: 1px solid #eee;
  width: 10%;
  margin: 1.5rem auto;
  display: block;
}
.btc{

    font-size: 4rem;
    font-weight: 600;
    padding: 1rem 0;
}
.card .btn{
    display: block;
    width: 80%;
    margin: 2rem auto;
}


.card .card {
  border: 1px solid #fff;
  color: #fff;
  text-align: center;
  padding: 1rem;
}
.card:hover {
  background-color: #141414;
}
