.project-heading {
  text-align: center;
  padding: 4rem 0 2rem 0;
}
.Project-containter {
  max-width: 1140px;
  margin: auto;
  display: grid;
  /* display: flex; */
  /* flex-direction: column; */
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  padding-bottom: 5rem;
}
.project-card {
  background: #1a1919;
  padding: 1.2rem 1rem;
  /* width: 40%; */
}
.project-card img {
  width: 100%;
}
.project-title {
  color: white;
  padding: 1rem 0rem;
}
.pro-btns {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.pro-details p {
  padding: 1rem;
  font-size: 1.1rem;
  text-align: justify;
}
@media screen and (max-width: 740px) {
  .Project-containter {
    max-width: 90%;
    margin: auto;
    grid-template-columns: 1fr;
    justify-content: center;
  }
  .project-card {
    background: #1a1919;
    padding: 1.2rem 1rem;
    width: 90%;
  }
}
