.footer {
  width: 100%;
  padding: 20rem, 0;
  background-color: rgb(19, 19, 19, 0.8);
  /* position: relative; */
  /* top: 20px; */
  
}
.footer-container {
  max-width: 1140px;
  /* margin-top: 20px ; */
  display: grid;
  grid-template-columns: 1fr  1fr;
  grid-gap: 40px;
  padding-bottom: 40px ;
  /* justify-content: right; */
  /* align-items: center; */
  
}
.left {
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  margin-top: 20px;
  /* margin-left: 40px; */
}
.location {
  display: flex;
  margin-bottom: 0.8rem;
}
.right {
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  margin-top: 20px;

}
.social {
  margin-top: 1rem;
}
@media screen and (max-width: 640px) {
  .footer-container {
    display: grid;
    grid-template-columns: 1fr;
  }
}
h4 {
  font-size: 1.2rem;
  padding-bottom: 0.7rem;
}
p {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}
